/*Blog details page*/


.blog-details div{
margin: 20px 0;
max-width: 800px;
display: flex;
justify-content: center;
text-align: justify;

}

.blog-details h2 {
font-size: 20px;
color:#f1356d;
margin-bottom: 10px;
}

.blog-details button {
background: #f1356d;
color: #fff;
border: 0;
padding: 8px;
border-radius: 8px;
cursor: pointer;
}

